<template>
  <div>
    <section class="hero is-fullheight">
      <!-- Hero header: will stick at the top -->
      <div class="hero-head">
        <header class="nav">
          <div class="container">
            <div class="nav-left">
              <gene-list :options="genes" @select="onOptionSelect"></gene-list>
            </div>
            <span class="nav-toggle">
              <span></span>
              <span></span>
              <span></span>
            </span>
            <div class="nav-right nav-menu">
              <a class="nav-item is-active">
                Home
              </a>
            </div>
          </div>
        </header>
      </div>

      <!-- Hero content: will be in the middle -->
      <div class="hero-body has-text-left">
        <section>
          <div class="container is-fluid">
            <div class="heading">
              <h1 class="title">Gene Name: {{ geneName }}</h1>
              <label class="label">NP#</label>
              <p class="control">
              <span class="select">
                <select @change="setNP">
                  <option disabled value="">Please select one</option>
                  <option v-for="i_np in np">{{ i_np }}</option>
                </select>
              </span>
              </p>
            </div>
            <div style="padding: 0% 10% 0% 10%">
              <!-- <p> -->
              <!-- <span v-for="mod in variants" :class="[mod.classification]">{{ mod.text }}<div v-if="mod.classification === 'modification'" v-html="mod.tooltip" class="tooltiptext"></div></span> -->
              <!-- </p> -->
              <hr>
              <p>
              <span v-for="mod in allSeq" :class="[mod.classification]">{{ mod.text }}<div v-if="mod.classification === 'modification'" v-html="mod.tooltip" class="tooltiptext"></div></span>
              <!-- <span v-for="piece in allSeq" :style="{color: piece.col}">{{ piece.text }}</span> -->
              </p>
              <hr>
              <p>
              <span>|</span>
              <span v-for="seq in sequences" class="tooltip"> {{ seq }} |</span>
              </p>
              <hr>
            </div>
            <pep-table
                                           :data="selected.variants"
                                           :columns="headers"
                                           :filterKey="searchQuery"></pep-table>
          </div>
        </section>
      </div>
    </section>
  </div>
</template>

<script>
import _ from 'lodash'
const zlib = require('zlib')
import GeneList from './gene-list/gene-list.vue'
import PepTable from './pep-table/pep-table.vue'
export default {
  name: 'hello',
  data () {
    return {
      all: [{'': -1, SEQ: ['asd'], NP_Counts: 1, Gene_Name: 'aaa001'}],
      selected: {'': -1, seq: 'asd', nu: '', variants: []},
      sequences: [],
      geneName: 'AATK',
      genes: [],
      fasta_seq: '',
      np: [],
      headers: ['REF', 'ALT', 'protein_ref', 'protein_pos', 'protein_alt'],
      searchQuery: ''
    }
  },
  created () {
    var oReq = new XMLHttpRequest()

    // oReq.addEventListener('progress', updateProgress)
    // oReq.addEventListener('load', transferComplete.call(this))
    // oReq.addEventListener('error', transferFailed)
    // oReq.addEventListener('abort', transferCanceled)

    oReq.open('get', './static/A_Gene_variants.json.gz.txt', true)
    // oReq.open('get', '/static/Hu_Output_Ver7.txt', true)
    oReq.onreadystatechange = (aEvt) => {
      if (oReq.readyState === XMLHttpRequest.DONE) {
        const buffer = Buffer.from(oReq.responseText, 'base64')
        zlib.unzip(buffer, (err, buffer) => {
          if (!err) {
            var data = JSON.parse(buffer.toString())
            this.all = data
            this.genes = _.keys(this.all)
            this.np = _.keys(this.all['AATK'])
            this.selected = this.all['AATK'][this.np[0]]
            this.selected.variants = _.map(this.selected.variants, f => { f.protein_pos = +f.protein_pos; return f })
            // this.headers = _.keys(this.selected.variants[0])
            this.fasta_seq = this.selected.seq
            this.sequences = this.selected.nu
          } else {
            console.log(err)
          }
        })
        //  var temp = oReq.responseText.replace(/\n$/, '').split('\n')
        //  var keys = temp.shift().split('\t')
        //  var o = _.map(temp, (t) => _.zipObject(keys, _.map(t.replace(/'/g, '').split('\t'))))
        //  // _.forEach(_.groupBy(o, s => s.Gene), (v, k) => { console.log(k, _.groupBy(v, t => t.NP_Accession)) })
        //  this.all = _.reduce(o, (r, v, k) => {
        //    (r[v.Gene] || (r[v.Gene] = {variants: [], table: {}, peptides: [], FASTA_SEQ: v.FASTA_SEQ}))
        //    r[v.Gene].peptides = _.union(r[v.Gene].peptides, v.UNIQ_SEQ.replace(/\s/g, '').split(','))

        //    r[v.Gene].table[v.protein_pos] || (r[v.Gene].table[v.protein_pos] = '<table class="table is-bordered is-striped is-narrow"><thead><tr><th>protein_alt</th><th>CHROM</th><th>POS</th><th>ALT</th><th>TAC</th></tr></thead><tr>')
        //    r[v.Gene].table[v.protein_pos] += '<td>' + v.protein_alt + '</td><td>' + v.CHROM + '</td><td>' + v.POS + '</td><td>' + v.ALT + '</td><td>' + _.round(v.alt_TAC_ratio, 3) + '</td></tr>'

        //    r[v.Gene].variants.push(
        //      parseInt(v.protein_pos)
        //      // protein_ref: v.protein_ref,
        //      // protein_alt: v.protein_alt,
        //      // CHROM: v.CHROM,
        //      // POS: parseInt(v.POS),
        //      // ALT: v.ALT,
        //      // alt_TAC_ratio: parseFloat(v.alt_TAC_ratio)
        //    )
        //    return r
        //  }, {})
        //  // console.log(this.all)
        //  // console.log(_.map(this.all, s => _.groupBy(s, 'UNIQ_SEQ')))
        //  // console.log(_.map(this.all, s => _.unionBy(s, t => t.UNIQ_SEQ.split(','))))
        //  this.genes = _.keys(this.all)
        //  this.selected = this.all['AADACL3']
        //  this.fasta_seq = this.selected.FASTA_SEQ
        //  this.sequences = this.selected.peptides
      }
    }
    oReq.onprogress = function (event) {
      event.loaded
      event.total
      console.log(event.loaded / event.total)
    }
    oReq.send()
  },
  components: {
    GeneList,
    PepTable
  },
  methods: {
    onOptionSelect (option) {
      console.log('Selected option:', option)
      this.geneName = option
      this.np = _.keys(this.all[option])
      this.selected = this.all[this.geneName][this.np[0]]
      this.selected.variants = _.map(this.selected.variants, f => { f.protein_pos = +f.protein_pos; return f })
      this.fasta_seq = this.selected.seq
      this.sequences = this.selected.nu
    },
    setNP (NP) {
      this.selected = this.all[this.geneName][NP.target.value]
      this.selected.variants = _.map(this.selected.variants, f => { f.protein_pos = +f.protein_pos; return f })
      this.fasta_seq = this.selected.seq
      this.sequences = this.selected.nu
    },
    // substrings (w) {
    // const i = this.selected[0].FASTA_SEQ.indexOf(w)
    // if (i !== -1) {
    // const s1 = this.selected[0].FASTA_SEQ.slice(0, i)
    // const s2 = this.selected[0].FASTA_SEQ.slice(i, i + w.length)
    // const s3 = this.selected[0].FASTA_SEQ.slice(i + w.length)
    // this.fasta_seq = [s1, s2, s3]
    // } else {
    // this.resetSS()
    // }
    // },
    resetSS () {
      this.fasta_seq = this.selected.FASTA_SEQ
    }
  },
  computed: {
    allSeq () {
      // return [{col: 'none', text: 'B'}, {col: 'orange', text: 'O'}, {col: 'red', text: 'R'}]
      const peptides = _.chain(this.sequences).uniq().map(s => { const i = this.fasta_seq.indexOf(s); return {w_end: 0, t: 'p', pos: i, end: i + s.length} }).value().sort((a, b) => b.pos - a.pos)
      var vrnts = _.reduce(this.selected.variants, (r, v, k) => {
        (r[v.protein_pos] || (r[v.protein_pos] = {t: 'v', pos: v.protein_pos, table: '<table class="table is-bordered is-striped is-narrow"><thead><tr><th>protein_ref</th><th>protein_pos</th><th>protein_alt</th></tr></thead>'}))
        r[v.protein_pos].table += '<tr><td>' + v.protein_ref + '</td><td>' + v.protein_pos + '</td><td>' + v.protein_alt + '</td></tr>'
        return r
      }, {})
      vrnts = Object.values(vrnts).sort((a, b) => a.pos - b.pos)
      console.log('num v', vrnts.length)
      // const temp = _.concat(peptides, vrnts).sort((a, b) => a.pos - b.pos)
      var positions = []
      var color = 0
      _.forEach(peptides, (v, i) => {
        // if (a[i+1].t === 'v')
        var tempPep = v
        tempPep.col = color
        while (true) {
          if (vrnts.length === 0) {
            break
          }
          var vrnt = vrnts.pop()
          if (vrnt.pos < v.pos) {
            vrnts.push(vrnt)
            break
          }
          if (vrnt.pos > v.end) {
            positions.push(vrnt)
          }
          if (tempPep.pos <= vrnt.pos && vrnt.pos <= tempPep.end) {
            positions.push({col: color, w_end: 0, t: 'p', pos: vrnt.pos, end: tempPep.end})

            tempPep.end = vrnt.pos
            positions.push(vrnt)
          }
        }
        positions.push(tempPep)
        color = (color + 1) % 3
        // positions.push(v)
      })
      var count = 0
      _.forEach(vrnts.reverse(), f => positions.push(f))
      var res = []
      var tempSeq = this.fasta_seq
      _.forEach(positions, e => {
        if (e.t === 'p') {
          var s = [e.pos, e.end]
          if (s[0] !== -1) {
            res.push({classification: 'normal', text: tempSeq.slice(s[1])})
            if (e.w_end === 0) {
              res.push({classification: 'peptide' + ' col' + e.col, text: tempSeq.slice(s[0], s[1])})
            //  res.push({classification: 'peptide', text: '[' + tempSeq.slice(s[0], s[1]) + ']'})
            } else if (e.w_end === 1) {
              res.push({classification: 'peptide', text: '[' + tempSeq.slice(s[0], s[1])})
            } else if (e.w_end === 2) {
              res.push({classification: 'peptide', text: tempSeq.slice(s[0], s[1]) + ']'})
            }
            tempSeq = tempSeq.slice(0, s[0])
          }
        } else if (e.t === 'v') {
          const s = e.pos
          count += 1
          res.push({text: tempSeq.slice(s), classification: 'normal'})
          res.push({
            text: tempSeq.slice(s - 1, s),
            tooltip: e.table + '</table>',
            classification: 'modification'
          })
          tempSeq = tempSeq.slice(0, s - 1)
        }
      })
      res.push({classification: 'normal', text: tempSeq})
      console.log('f v', count)
      return res.reverse()
    },
    variants () {
      var positions = _.reduce(this.selected.variants, (r, v, k) => {
        (r[v.protein_pos] || (r[v.protein_pos] = {protein_pos: v.protein_pos, table: '<table class="table is-bordered is-striped is-narrow"><thead><tr><th>protein_ref</th><th>protein_pos</th><th>protein_alt</th></tr></thead>'}))
        r[v.protein_pos].table += '<tr><td>' + v.protein_ref + '</td><td>' + v.protein_pos + '</td><td>' + v.protein_alt + '</td></tr>'
        return r
      }, {})
      positions = Object.values(positions).sort((a, b) => a.protein_pos - b.protein_pos)
      var res = []
      var tempSeq = this.fasta_seq
      _.forEachRight(positions, e => {
        const s = e.protein_pos
        res.push({text: tempSeq.slice(s), classification: 'normal'})
        res.push({
          text: tempSeq.slice(s - 1, s),
          tooltip: e.table + '</table>',
          classification: 'modification'
        })
        tempSeq = tempSeq.slice(0, s - 1)
      })
      res.push({classification: 'normal', text: tempSeq})
      return res.reverse()
    }
    // substrings () {
    // const seq = this.selected[0].FASTA_SEQ
    // return _.map(this.sequences, s => {
    // const start = seq.indexOf(s)
    // return { start: start, end: start + s.length, seq: s }
    // })
    // }
  }
}
</script>

